import Vue from 'vue'
import Router from 'vue-router'
import Cookies from 'js-cookie'

Vue.use(Router)


export default new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      redirect: '/404'
    },
    /*定义错误404页面*/
    {
      path: '/404',
      name: 'error',
      component: ()=> import('@/views/404/404')
    },
    {
      path: '/',
      name: 'login',
      component: () => import('@/views/login/login')
    },
    {
      path: '/home_main',
      name: 'home_main',
      meta: {
        requireAuth: true,
      },
      component: () => import('@/views/home_main'),
      children:[
        {
          path:'home',
          name: 'home',
          component: ()=> import('@/views/main-components/home')
        },
        {
          path: 'personManager',
          name: 'personManager',
          component: ()=> import('@/views/main-components/personManager')
        },
        {
          path: 'departManager',
          name: 'departManager',
          component: ()=> import('@/views/main-components/departManager')
        },
        {
          path: 'reportList',
          name: 'reportList',
          component: ()=> import('@/views/report/reportList')
        },
        {
          path: 'articlePublish',
          name: 'articlePublish',
          component: ()=> import('@/views/report/articlePublish')
        },
        {
          path: 'reportCheck',
          name: 'reportCheck',
          component: ()=> import('@/views/report/reportCheck' +
          '')
        },
        {
          path: 'personInfo',
          name: 'personInfo',
          component: ()=> import('@/views/login/personInfo')
        }
      ]
    }
  ]
})



