import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store =  new Vuex.Store({
  state: {
      openBreadList:''
  },
  mutations: {
      setOpenList(state,name){
        state.openBreadList = name;
      }
  },
  getters:{
    getOpenList(state){
      return state.openBreadList;
    }
  },
  actions: {

  },
  modules: {

  }
});

export default store;



